import React from 'react';
import CourseCarousel from "../CourseCarousel";
import CourseCard from "../CourseCard";
import {useGetCourses} from "../Providers/CourseProvider";
import AddItem from "../Atomics/AddItem";

const CourseOverview = () => {
    const courses = useGetCourses();

    const constructCourseCards = () => {
        const temp = [];

        for(let key in courses) {
            const {title, description, symbol} = courses[key];
            temp.push(
                <CourseCard key={key} title={title} to={`/course/${key}`} symbol={symbol}>
                    {description}
                </CourseCard>
            );
        }

        return temp;
    };

    return (
        <CourseCarousel>
            {constructCourseCards()}
        </CourseCarousel>
    )
};

export default CourseOverview;