import React from "react";
import styled from "styled-components";
import CardBase from "./CardBase";
import CardSymbol from "./CardSymbol";

const Base = styled(CardBase)`
    box-sizing: border-box;
    margin: 8px 0;
    width: 100%;
    display: flex;
`;

const MyCourseProgress = styled.div`
    
`;

const MyCourseSymbol = styled(CardSymbol)`
    width: 80px;
`;

const MyCourseTitle = styled.h1`
    color: #666;
    font-size: 13pt;
    margin-bottom: 8px;
`;

const MyCourseContent = styled.div`
    padding: 16px;
    font-size: 11pt;
`;

const MyCourseCard = ({symbol, title, progress,...props}) => {
    return (
        <Base>
            <MyCourseSymbol>
                flash_on
            </MyCourseSymbol>
            <MyCourseContent>
                <MyCourseTitle>
                    {title}
                </MyCourseTitle>
                <MyCourseProgress>
                    Fortschritt: {progress * 100}%
                </MyCourseProgress>
            </MyCourseContent>
        </Base>
    )
};

export default MyCourseCard;
